___CSS_LOADER_EXPORT___.push([module.id, `.iztro-center-palace {
  grid-area: ct;
  position: relative;
  width: 100%;
  height: 100%;
}
.iztro-center-palace-centralize {
  text-align: center;
}
.iztro-center-palace ul.basic-info {
  margin: 10px;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 20px;
}
.iztro-center-palace ul.basic-info li {
  list-style: none;
}
.iztro-center-palace .center-title {
  padding: 5px 0;
  margin: 0;
  font-size: var(--iztro-star-font-size-big);
  font-weight: bold;
  text-align: center;
  border-bottom: 1px dashed var(--iztro-color-border);
}

.horo-buttons {
  margin: 10px;
  font-size: var(--iztro-star-font-size-small);
  display: flex;
  justify-content: space-around;
}
.horo-buttons .center-button {
  display: block;
  text-align: center;
  padding: 5px;
  border: 1px solid var(--iztro-color-border);
  cursor: pointer;
  transition: all 0.25s ease-in-out;
  color: var(--iztro-color-text);
  user-select: none;
}
.horo-buttons .center-button:not(.disabled):hover {
  color: var(--iztro-color-major);
  background-color: var(--iztro-color-border);
}
.horo-buttons .center-button.disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
.horo-buttons .center-horo-hour {
  display: flex;
  align-items: center;
}

.iztro-copyright {
  position: absolute;
  bottom: 3px;
  right: 3px;
  font-size: 12px;
  color: rgba(0, 0, 0, 0.2);
  text-decoration: none;
  text-shadow: 1px 1px rgba(255, 255, 255, 0.3);
}

#palace-line {
  stroke: var(--iztro-color-awesome);
  opacity: 0.6;
  transition: all 0.25s ease-in-out;
}
#palace-line.decadal {
  stroke: var(--iztro-color-decadal);
}

.solar-horoscope {
  display: flex;
  align-items: center;
  gap: 10px;
}

.solar-horoscope-centralize {
  justify-content: center;
}

.solar-horoscope .today {
  display: inline-block;
  font-size: var(--iztro-star-font-size-small);
  cursor: pointer;
  border: 1px solid var(--iztro-color-border);
  padding: 0 5px;
  transition: all 0.25s ease-in-out;
}

.solar-horoscope .today:hover {
  color: var(--iztro-color-major);
  background-color: var(--iztro-color-border);
}`, "",{"version":3,"sources":["webpack://./src/IzpalaceCenter/IzpalaceCenter.css"],"names":[],"mappings":"AAAA;EACE,aAAa;EACb,kBAAkB;EAClB,WAAW;EACX,YAAY;AACd;AACA;EACE,kBAAkB;AACpB;AACA;EACE,YAAY;EACZ,UAAU;EACV,aAAa;EACb,qCAAqC;EACrC,gBAAgB;AAClB;AACA;EACE,gBAAgB;AAClB;AACA;EACE,cAAc;EACd,SAAS;EACT,0CAA0C;EAC1C,iBAAiB;EACjB,kBAAkB;EAClB,mDAAmD;AACrD;;AAEA;EACE,YAAY;EACZ,4CAA4C;EAC5C,aAAa;EACb,6BAA6B;AAC/B;AACA;EACE,cAAc;EACd,kBAAkB;EAClB,YAAY;EACZ,2CAA2C;EAC3C,eAAe;EACf,iCAAiC;EACjC,8BAA8B;EAC9B,iBAAiB;AACnB;AACA;EACE,+BAA+B;EAC/B,2CAA2C;AAC7C;AACA;EACE,YAAY;EACZ,mBAAmB;AACrB;AACA;EACE,aAAa;EACb,mBAAmB;AACrB;;AAEA;EACE,kBAAkB;EAClB,WAAW;EACX,UAAU;EACV,eAAe;EACf,yBAAyB;EACzB,qBAAqB;EACrB,6CAA6C;AAC/C;;AAEA;EACE,kCAAkC;EAClC,YAAY;EACZ,iCAAiC;AACnC;AACA;EACE,kCAAkC;AACpC;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,SAAS;AACX;;AAEA;EACE,uBAAuB;AACzB;;AAEA;EACE,qBAAqB;EACrB,4CAA4C;EAC5C,eAAe;EACf,2CAA2C;EAC3C,cAAc;EACd,iCAAiC;AACnC;;AAEA;EACE,+BAA+B;EAC/B,2CAA2C;AAC7C","sourcesContent":[".iztro-center-palace {\n  grid-area: ct;\n  position: relative;\n  width: 100%;\n  height: 100%;\n}\n.iztro-center-palace-centralize {\n  text-align: center;\n}\n.iztro-center-palace ul.basic-info {\n  margin: 10px;\n  padding: 0;\n  display: grid;\n  grid-template-columns: repeat(2, 1fr);\n  column-gap: 20px;\n}\n.iztro-center-palace ul.basic-info li {\n  list-style: none;\n}\n.iztro-center-palace .center-title {\n  padding: 5px 0;\n  margin: 0;\n  font-size: var(--iztro-star-font-size-big);\n  font-weight: bold;\n  text-align: center;\n  border-bottom: 1px dashed var(--iztro-color-border);\n}\n\n.horo-buttons {\n  margin: 10px;\n  font-size: var(--iztro-star-font-size-small);\n  display: flex;\n  justify-content: space-around;\n}\n.horo-buttons .center-button {\n  display: block;\n  text-align: center;\n  padding: 5px;\n  border: 1px solid var(--iztro-color-border);\n  cursor: pointer;\n  transition: all 0.25s ease-in-out;\n  color: var(--iztro-color-text);\n  user-select: none;\n}\n.horo-butto